/*  */
const BidList = r => require.ensure([], () => r(require('@/page/mall/bid/BidList')), 'BidList') // 
const BidDetail = r => require.ensure([], () => r(require('@/page/mall/bid/BidDetail')), 'BidDetail') // 
const BidRule = r => require.ensure([], () => r(require('@/page/mall/bid/BidRule')), 'BidRule') // -
const BidResult = r => require.ensure([], () => r(require('@/page/mall/bid/BidResult')), 'BidResult') // 

/* - */
const BidMemberList = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberList')), 'BidMemberList') // 
const BidMemberEdit = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberEdit')), 'BidMemberEdit') // -
const BidMemberView = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberView')), 'BidMemberView') // -
const BidMemberHistory = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberHistory')), 'BidMemberHistory') // -
const BidMemberResult = r => require.ensure([], () => r(require('@/page/mall/bid/member/BidMemberResult')), 'BidMemberResult') // -

export default [
  { path: '/BidList', name: 'BidList', component: BidList },
  { path: '/BidDetail', name: 'BidDetail', component: BidDetail },
  { path: '/BidRule', name: 'BidRule', component: BidRule },
  { path: '/BidResult', name: 'BidResult', component: BidResult },
  { path: '/BidMemberList', name: 'BidMemberList', component: BidMemberList },
  { path: '/BidMemberEdit', name: 'BidMemberEdit', component: BidMemberEdit },
  { path: '/BidMemberView', name: 'BidMemberView', component: BidMemberView },
  { path: '/BidMemberHistory', name: 'BidMemberHistory', component: BidMemberHistory },
  { path: '/BidMemberResult', name: 'BidMemberResult', component: BidMemberResult }
]
