// import App from '../App'

import Vue from 'vue'
import VueRouter from 'vue-router'
import { setPage } from '../common/js/catch' // 
import gysRouters from './gys/routers' // 
import jbrRouters from './jbr/routers' // 
import gg from './gg/gg' // 
import dd from './dd/home' // 
import mallRouters from './mall/routers' // 
import mhRouters from './mh/home' // 

import bid from './bid/routers' // 
import xj from './xj/routers' // 
import bidhall from './bidhall/routers' // 
import zjRouters from './zj/routers' //  
import erp from './erp/routers' // ERP

Vue.use(VueRouter)
const RouterModel = new VueRouter({

  // mode: 'history',
  // base: '/gqcg/',

  routes: [
    ...xj,
    ...gysRouters,
    ...jbrRouters,
    ...gg,
    ...dd,
    ...mallRouters,
    ...mhRouters,
    ...bid,
    ...bidhall,
    ...zjRouters,
    ...erp
  ],
  // vue
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})
// router
RouterModel.beforeEach((to, from, next) => {
  /*  */
  // this.$store.commit('updateLoadingStatus', true)
  if (to.matched.some(record => record.meta.lines)) { // 
    next()
    setPage(true)
    // this.$store.commit('SET_PAGELINES', false)
  } else {
    next() //  next()
    setPage(false)
    // this.$store.commit('SET_PAGELINES', true)
  }
})
RouterModel.afterEach(route => {
  /*  */
  // this.$store.commit('updateLoadingStatus', false)
})

export default RouterModel
