import Vue from 'vue'
import VeeValidate, { Validator } from 'vee-validate'
import zh_CN from 'vee-validate/dist/locale/zh_CN'
// 
Validator.localize(zh_CN)

const config = {
  errorBagName: 'errors', // change if property conflicts.
  fieldsBagName: 'fieldBags', //  
  delay: 0, // ms
  strict: true, // 
  enableAutoClasses: false,
  locale: 'zh_CN', // 
  classNames: {
    touched: 'touched', // the control has been blurred
    untouched: 'untouched', // the control hasn't been blurred
    valid: 'valid', // model is valid
    invalid: 'invalid', // model is invalid
    pristine: 'pristine', // control has not been interacted with
    dirty: 'dirty' // control has been interacted with
  },
  events: 'input|blur', //* *input|blur**    blurinput
  inject: true
}

// validate
const dictionary = {
  zh_CN: {
    messages: {
      TelOrMobile: () => '',
      email: () => '',
      phone: () => '',
      yhzhCheck: () => '',
      sbmCheck: () => '',
      sfzLength: () => '18',
      xydmLength: () => '18',
      msgCode: () => '6',
      mmLength: () => ' 6 ',
      address: () => '',
      pinLength: () => '6',
      fhsjCheck: () => '',
      yjdhsj: () => '',
      equal: () => '',
      gzlCheck: () => '',
      required: (field) => field
    },
    attributes: {
      email: '',
      password: '',
      userAccount: '',
      username: '',
      phone: '',
      ztmc: '',
      ztdm: '',
      mm: '',
      cfmm: '',
      lxr: '',
      lxrdh: '',
      lxryx: '',
      ggyx: '',
      yzm: '',
      sfzh: '',
      yx: '',
      sjhm: '',
      zjmc: '',
      smj: '',
      PIN: 'Pin',
      name: '',
      sjh: '',
      yb: '',
      jtdz: '',
      address: '',
      fptt: '',
      nsrsbm: '',
      fptt1: '',
      nsrsbm1: '',
      zcdd: '',
      zcdh: '',
      khyh: '',
      yhzh: '',
      sssy: '',
      ssbz: '',
      fhdmc: '',
      fhsj: '',
      fhdd: '',
      yjdhsj: '',
      ydh: '',
      bcsl: '',
      wtms: '',
      sfybz: '',
      fwlx: '',
      fjfs: '',
      shdz: '',
      fjdz: '',
      xxdz: '',
      khxm: '',
      tjsl: '',
      gzl: '',
      rznr: '',
      fl: '',
      xm: ''
    }
  }
}
Validator.localize(dictionary)

Vue.use(VeeValidate, config)

Validator.extend('yhzhCheck', {
  messages: {
    zh_CN: field => field + ''
  },
  validate: value => {
    return /^([1-9]{1})(\d{14}|\d{18})$/.test(value)
  }
})

Validator.extend('sbmCheck', {
  messages: {
    zh_CN: field => field + ''
  },
  validate: value => {
    return /^[A-Za-z0-9]+$/.test(value)
  }
})

Validator.extend('gzlCheck', {
  messages: {
    zh_CN: field => field + ''
  },
  validate: value => {
    return value > 0 && value <= 7
  }
})

Validator.extend('phone', {
  messages: {
    zh_CN: field => field + '11'
  },
  validate: value => {
    return value.length === 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value)
  }
})

Validator.extend('TelOrMobile', {
  messages: {
    zh_CN: field => field + '11'
  },
  validate: value => {
    return new RegExp(/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/).test(value) || new RegExp(/^1[3456789]\d{9}$/).test(value)
  }
})

// pin6
Validator.extend('pinLength', {
  messages: {
    zh_CN: field => field + '6'
  },
  validate: value => {
    return value.length === 6
  }
})

// 18
Validator.extend('sfzLength', {
  messages: {
    zh_CN: field => field + '18'
  },
  validate: value => {
    return value.length === 18
  }
})
// 18
Validator.extend('xydmLength', {
  messages: {
    zh_CN: field => field + '18'
  },
  validate: value => {
    return value.length === 18
  }
})

// 6
Validator.extend('mmLength', {
  messages: {
    zh_CN: field => field + '6'
  },
  validate: value => {
    return value.length >= 6
  }
})
// 
Validator.extend('equal', {
  messages: {
    zh_CN: field => ''
  },
  validate: value => {
    return value === this.mm
  }
})
// 6
Validator.extend('msgCode', {
  getMessage: field => field + '6',
  validate: value => !!/^[a-zA-Z0-9]{6}$/.test(value)
})
// 
Validator.extend('nickname', {
  getMessage: field => field + ' ',
  validate: value => !!/^[a-zA-Z0-9\u4e00-\u9fa5]+$/.test(value)
})
// 
Validator.extend('maxChar', {
  getMessage: (field, params) => {
    return field + '' + params[0] + '.'
  },
  validate: (value, params) => {
    // eslint-disable-next-line no-control-regex
    return !(value.replace(/[^\x00-\xff]/g, 'xx').length > parseInt(params[0]))
  }
})
// 
Validator.extend('minChar', {
  getMessage: (field, params) => {
    return field + '' + params[0] + '.'
  },
  validate: (value, params) => {
    // eslint-disable-next-line no-control-regex
    return !(value.replace(/[^\x00-\xff]/g, 'xx').length < parseInt(params[0]))
  }
})
Validator.extend('email', {
  messages: {
    zh: ''
  },
  validate: value => {
    return /\w[-\w.+]*@([A-Za-z0-9][-A-Za-z0-9]+\.)+[A-Za-z]{2,14}/.test(value)
  }
})
Validator.extend('address', {
  messages: {
    zh: field => field + ''
  },
  validate: value => {
    return value.length > 0
  }
})
