export function deleteSearch(query) {
  // const test = [['wdxm', [11, 2, 5, 9, 52, 1, 78]], ['dbsx', [3, 33, 56]]]
  const searches = storage.get(SEARCH_KEY, [])
  deleteFromArray(searches, (item) => {
    return item === query
  })
  storage.set(SEARCH_KEY, searches)
  return searches
}

export function loadSearch(key) {
  // const searches = [['wdxm', [11, 2, 5, 9, 52, 1, 78]], ['dbsx', [3, 33, 56]]]
  const userid = '001' // id
  const searches = storage.get(userid, [])
  let searchesBack = []
  for (let i = 0; i < searches.length; i++) {
    if (searches[0] !== null) {
      if (searches[i][0] === key) {
        hasKey = i
      }
    } else {
      return []
    }
  }
  if (hasKey > -1) {
    // return searches[hasKey][1]
    searchesBack = searches[hasKey][1]
  }

  return searchesBack
}

